import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

const hurl = "https://restaurantcl.herokuapp.com/restaurantDetails";
const burl = "https://restaurantcl.herokuapp.com/placeorder";

class PlaceOrder extends Component {
    constructor() {
        super();

        this.state={
            order_id:Math.floor(Math.random()*10000),
            rest_name:'',
            name:'',
            phone:'',
            address:'',
            no_persons:''
        }

    }

    componentDidMount(){
        let restid=this.props.match.params.id;
        axios.get(`${hurl}/${restid}`)
        .then((response) => {
            {this.setState({rest_name:response.data[0].name})}
        })
    }

    handleChangeName = (event) =>{
        this.setState({name:event.target.value});        
    }
    handleChangePhone = (event) =>{
        this.setState({phone:event.target.value});        
    }
    handleChangeAddress = (event) =>{
        this.setState({address:event.target.value});        
    }
    handleChangePersons = (event) =>{
        this.setState({no_persons:event.target.value});        
    }
    handleSubmit = () => {
        var data={
            "_id":this.state.order_id,
            "rest_name":this.state.rest_name,
            "name":this.state.name,
            "phone":this.state.phone,
            "address":this.state.address,
            "person":this.state.no_persons
        }

        fetch(burl,{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data)
        })
        .then(this.props.history.push('/ViewOrder'))
    }



    render() {
        return (
            <div className="container">
                <div className="panel panel-primary">
                    <div className="panel-heading">
                        <h4>Place Booking</h4>
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label className="control-label">Order Id</label>
                            <input type="text" name="order_id" readOnly className="form-control" value={this.state.order_id}/>
                        </div>
                        <div className="form-group">
                            <label className="control-label">Restaurent Name</label>
                            <input type="text" name="rest_Name" readOnly className="form-control" value={this.state.rest_name}/>
                        </div>
                        <div className="form-group">
                            <label className="control-label">User Name</label>
                            <input type="text" name="name" className="form-control" required value={this.state.name} onChange={this.handleChangeName}/>
                        </div>
                        <div className="form-group">
                            <label className="control-label">Phone Number</label>
                            <input type="text" name="Phone" className="form-control" required value={this.state.phone} onChange={this.handleChangePhone}/>
                        </div>
                        <div className="form-group">
                            <label className="control-label">Address</label>
                            <input type="text" name="address" className="form-control" required value={this.state.address} onChange={this.handleChangeAddress}/>
                        </div>
                        <div className="form-group">
                            <label className="control-label">No of Persons</label>
                            <select value={this.state.no_persons} className="form-control" required name="no_persons"  onChange={this.handleChangePersons}>
                                <option>select No of persons</option>
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                            </select>

                        </div>
                        <Link to={`/Rest/${this.props.match.params.id}`} className="btn btn-danger">Cancel</Link>
                        &nbsp;
                        <button className="btn btn-success" onClick={this.handleSubmit}>
                            Submit
                        </button>
                    </div>
                </div>

            </div>
        )
    }
}

export default PlaceOrder;