import React from 'react';
import './Quicksearch.css';
import { Link } from 'react-router-dom';

const Quicksearch = (props) => {

    const listmeals = ({ mealdata }) => {
        if (mealdata) {
            return mealdata.map((item) => {
                return (
                    <Link to={`/list/${item.mealtype}`}>
                        <div>
                            <div className="tilecontainer  col-sm-4 col-md-3 col-lg-3">
                                <div className="row">
                                    <div className="tilecomponent1">
                                        <img src={`./images/${item.name}.jfif`} className="blockimage" />
                                    </div>
                                    <div className="tilecomponent2 blocktext">
                                        <div className="componentheading">
                                            {item.name}
                                        </div>
                                        <div className="componentsubheading">
                                            Start your day with exclusive {item.name}!
                                    </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </Link>
                )
            })
        }
    }

    return (
        <div className="quicksearchcontainer container center-block">
            <p className="quicksearchheading">
                Quick Searches
            </p>
            <p className="quicksearchsubheading">
                Discover Reaturents by Meal type.
            </p>
            {listmeals(props)}
        </div>

    )
}

export default Quicksearch;