import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

const url = "https://restaurantcl.herokuapp.com/restaurantDetails";

class RestaurentDetails extends Component {
    constructor(props) {
        super(props)

        this.state = {
            restaurent: {
                Cuisine:[
                    {
                        name:''
                    },
                    {
                        name:''
                    }
                ]
            }
        }
    }

    onBack = (event) => {
        let mealid = sessionStorage.getItem('type');
        this.props.history.push(`/List/${mealid}`);
    }

    render() {
        var { restaurent } = this.state;
        return (
            <div className="container">
                <div className="panel panel-primary">
                    <div className="panel-heading">
                        <h2>{restaurent.name}</h2>
                    </div>
                    <div className="panel-body">
                        <div className="row">
                            <div className="col-md-12">
                                <div id="myCarousel" className="carousel slide" data-ride="carousel">
                                    <ol className="carousel-indicators">
                                        <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
                                        <li data-target="#myCarousel" data-slide-to="1"></li>
                                        <li data-target="#myCarousel" data-slide-to="2"></li>
                                    </ol>

                                    <div className="carousel-inner">
                                        <div className="item active img-responsive" style={{height:'400px',width:'1200px'}}>
                                            <img src={restaurent.thumb}/>
                                        </div>
                                        <div className="item img-responsive" style={{height:'400px',width:'1200px'}}>
                                            <img src={restaurent.thumb}/>
                                        </div>
                                        <div className="item img-responsive" style={{height:'400px',width:'1200px'}}>
                                            <img src={restaurent.thumb}/>
                                        </div>
                                    </div>
                                    <a className="left carousel-control" href="#myCarousel" data-slide="prev">
                                        <span className="glyphicon glyphicon-chevron-left"></span>
                                        <span className="sr-only">Previous</span>
                                    </a>
                                    <a className="right carousel-control" href="#myCarousel" data-slide="next">
                                        <span className="glyphicon glyphicon-chevron-right"></span>
                                        <span className="sr-only">Next</span>
                                    </a>

                                </div>
                            </div>
                            <div className="col-md-12" style={{ fontFamily: "sans-serif" }}>
                                <h3>{restaurent.name}</h3>
                                <h4>{restaurent.locality}</h4>
                                <h4>{restaurent.address}</h4>
                            </div>
                        </div>
                        <br />
                        <div>
                            <Tabs>
                                <TabList>
                                    <Tab>Overview</Tab>
                                    <Tab>Contact</Tab>
                                </TabList>
                                <TabPanel>
                                    <div>
                                        <div>About This Place</div>
                                        <br />
                                        <div>Cuisine</div>
                                        <div>{restaurent.Cuisine[0].name},{restaurent.Cuisine[1].name}</div>
                                        <div>Cost For Two: Rs {restaurent.cost}</div>
                                    </div>
                                </TabPanel>
                                <TabPanel>
                                    <div>
                                        <div>Contact</div>
                                        <br />
                                        <div>Phone: +91{restaurent.contact_number}</div>
                                        <div>{restaurent.locality}</div>
                                    </div>
                                </TabPanel>
                            </Tabs>
                        </div>
                        <br />
                        <br />
                        <div>
                            <button className="btn btn-danger btn-lg" onClick={this.onBack}>Back</button>
                            &nbsp;
                            <Link className="btn btn-success btn-lg" to={`/Order/${this.props.match.params.id}`}>Place Order</Link>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount() {
        let restid = this.props.match.params.id;
        axios.get(`${url}/${restid}`)
            .then((response) => {
                this.setState({ restaurent: response.data[0] })
            })
    }
}

export default RestaurentDetails;
