import React, { Component } from 'react';

import './Search.css';

const lurl = "https://restaurantcl.herokuapp.com/location";
const rurl = "https://restaurantcl.herokuapp.com/restaurant?city=";

class Search extends Component {
    constructor() {
        super()

        this.state = {
            title: 'Find the best restaurents, cafes and bars.',
            location: '',
            restaurents:''
        }
    }

    rendercity = (data) => {
        if (data) {
            return data.map((item) => {
                return (
                    <option value={item.city}>
                        {item.city_name}-{item.name}
                    </option>
                )
            })
        }
    }

    renderRestaurent = (data) => {
        if (data) {
            return data.map((item) => {
                return (
                    <option value={item._id}>
                        {item.name}-{item.locality}
                    </option>
                )
            })
        }
    }

    handleCity = (event) => {
        console.log(event.target.value);
        fetch(`${rurl}${event.target.value}`, { method: 'GET' })
            .then((res) => res.json())
            .then((data) => {
                this.setState({ restaurents: data })
            });
        console.log(this.state.restaurents);
    }

    handleRestaurent = (event) => {
        this.props.rid(event.target.value);
    }

    render() {
        console.log(this.state.location);
        return (
            <div className="background-img img-responsive text-center conatiner-fluid">
                <div className="logo center-block">
                    e!
            </div>
                <div className="heading center-block">
                    {this.state.title}
                </div>
                <div className="row locationselector">
                    <div className="col-md-6 col-lg-6">
                        <select className="searchrestaurents" onChange={this.handleCity}>
                            <option>Select City</option>
                            {this.rendercity(this.state.location)}
                        </select>
                    </div>
                    <div className="col-md-6 col-lg-6">
                        <select className="searchrestaurents" onChange={this.handleRestaurent}>
                            <option>Select Restaurent</option>
                                {this.renderRestaurent(this.state.restaurents)}
                        </select>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount() {
        fetch(lurl, { method: 'GET' })
            .then((res) => res.json())
            .then((data) => {
                this.setState({ location: data })
            });
    }
}

export default Search;