import React from 'react';
import { Link } from 'react-router-dom';
import './Listing.css';

const ListingDisplay = (props) => {

    const renderRest = ({ restdata }) => {
        if (restdata) {
            if (restdata.length > 0) {
                return restdata.map((item) => {
                    return (
                        <div className="main-details" id={item.id}>
                            <div className="main-tile-container">
                                <div className="row contents">
                                    <div className="col-md-3">
                                        <img className="image" src={item.thumb} />
                                    </div>
                                    <div className="col-md-9">
                                        <div className="image-main-descr">
                                            <Link to={`/Rest/${item._id}`}>{item.name}</Link>
                                        </div>
                                        <div className="address1">{item.city_name}</div>
                                        <div className="address2">{item.locality}</div>
                                        <div className="address2">{item.address}</div>
                                    </div>
                                </div>
                                <hr />
                                <div className="row contents">
                                    <div className="col-md-3">
                                        <div className="lower-section-heading">Cuisine Type</div>
                                        <div className="lower-section-heading">Cost For Two</div>
                                    </div>
                                    <div className="col-md-9">
                                        <div className="lower-section-values">{item.type[0].name},{item.type[1].name}</div>
                                        <div className="lower-section-values">Rs {item.cost}</div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    )
                })
            }
            else {
                return (
                    <div>
                        <center>
                            <h2>No Restaurent Found As Per Filter</h2>
                        </center>
                    </div>
                )
            }
        }
        else {
            return (
                <div>
                    <img src="/images/loader.gif" />
                </div>
            )
        }
    }

    return (
        <div className="container">
            <div className="mainheading">
                <div className="row">
                    <div className="col-md-11">
                        {renderRest(props)}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ListingDisplay;